.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.input-group {
  margin: 10px 0;
}

.input-group label {
  margin-right: 10px;
}

.input-group input {
  padding: 8px;
  font-size: 1rem;
  border-radius: 4px;
  border: 1px solid #ccc;
  width: 100px; /* 입력 필드 너비 조정 */
}

button {
  padding: 10px 20px;
  font-size: 1rem;
  margin-top: 20px;
  border-radius: 5px;
  border: none;
  background-color: #61dafb;
  color: #282c34;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #21a1f1;
}

.result-box {
  background-color: #3a3f4a;
  padding: 20px;
  margin-top: 30px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.result-box pre {
  background-color: #1e1e1e;
  padding: 15px;
  border-radius: 5px;
  text-align: left;
  overflow-x: auto;
  font-size: 0.9em;
  color: #a9a9a9;
}

.error {
  color: #ff6b6b;
  font-weight: bold;
}

.note {
  font-size: 0.8em;
  color: #bbb;
  margin-top: 30px;
  text-align: center;
}
